<template>
  <div class="home-all">
    <nav class="navbar" style="height: 8vh;background-color: cornflowerblue;min-height: 8vh;max-height: 80px">
      <a class="navbar-brand" style="display: inline-block;height: 100%;color: white;font-size: 14px" href="#">
        <img src="../assets/login.png" style="height: 100%;">
      </a>
      <div style="color: white;height: 6vh;line-height: 6vh;font-size: 14px">
        {{$t("mainPageTtile")}}
        <Icon type="md-desktop" />
      </div>
      <div class="form-inline my-2 my-lg-0" style="max-height: 6vh;line-height: 6vh">
        <language></language>
        <Button style="height: 100%;" @click="gotoLogin" v-if="showLogin"
                shape="circle" icon="ios-contact" size="large">{{$t("login")}}</Button>

        <div class="dropdown" v-else>
          <button class="btn btn-outline-light dropdown-toggle"
                  type="button" id="dropdownMenuButton" data-toggle="dropdown"
                  aria-haspopup="true" aria-expanded="false">
            {{ $t("hello") + "，" + helloUser }}
          </button>
          <div class="dropdown-menu dropdown-menu-right" style="font-size: 14px;line-height: normal">
            <a class="dropdown-item" href="#" @click="gotoPersonal">{{$t("personal")}}</a>
            <a class="dropdown-item" href="#" @click="gotoAdminPage">{{$t("adminPage")}}</a>
            <a class="dropdown-item" href="#" @click="exitSystem">{{$t("exit")}}</a>
          </div>
        </div>
      </div>
    </nav>
    <div class="home-container">
      <div class="row">
        <div class="col-xl-3 col-lg-3 col-md-6 col-sm-6">
          <web-site-item></web-site-item>
        </div>
        <div class="col-xl-3 col-lg-3 col-md-6 col-sm-6">
          <web-site-item></web-site-item>
        </div>
        <div class="col-xl-3 col-lg-3 col-md-6 col-sm-6">
          <web-site-item></web-site-item>
        </div>
        <div class="col-xl-3 col-lg-3 col-md-6 col-sm-6">
          <web-site-item></web-site-item>
        </div>
        <div class="col-xl-3 col-lg-3 col-md-6 col-sm-6">
          <web-site-item></web-site-item>
        </div>
        <div class="col-xl-3 col-lg-3 col-md-6 col-sm-6">
          <web-site-item></web-site-item>
        </div>
        <div class="col-xl-3 col-lg-3 col-md-6 col-sm-6">
          <web-site-item></web-site-item>
        </div>
      </div>
    </div>
    <v-loading :show="loading"></v-loading>
  </div>
</template>

<script>
import WebSiteItem from "@/components/slotItem/webSiteItem";
import Language from "@/components/public/language";
import VLoading from "@/components/public/v-loading";
export default {
  name: "HomePage",
  components: {VLoading, Language, WebSiteItem},
  data : function (){
    return{
      loading : false
    }
  },
  beforeMount() {
    this.$(".item-img").css("background")
  },
  methods :{
    gotoAdminPage : function (){
      this.$router.push("/admin");
    },
    gotoLogin : function (){
      this.$router.push("/login");
    },
    exitSystem : function (){
      this.loading = true;
      var _this = this;
      this.$axios.post("/login/exit",{}).then(function (res){
          _this.loading = false;
          if (res.data.code != "success"){
            _this.$Message.error(_this.$t(res.data.code));
          }else {
            _this.$cookies.remove("token");
            _this.$cookies.remove("user");
            window.location.reload();
          }
      }).catch(function (res){
        _this.loading = false;
        console.log(res);
        _this.$Message.error(_this.$t("err.systemErr"));
      })
    }
  },
  computed : {
    showLogin : function (){
      return this.$store.getters.getUser == undefined;
    },
    helloUser : function (){
      return this.$store.getters.getName;
    },
  }
}
</script>

<style scoped>
.home-container{
  width: 100%;
  height: 92vh;
  padding-left: 1%;
  padding-right: 1%;
  padding-top: 20px;
  padding-bottom: 50px;
  overflow: auto;
}
.home-all {
  width: 100%;
  height: 100vh;
  position: relative;
}
.home-all::after{
  width: 100%;
  height: 100vh;
  content:'';
  position:absolute;
  top:0;
  left:0;
  background: transparent url("../assets/backtmp.jpg") center center no-repeat;
  /*filter:blur(2px);*/
  z-index:-1;
  background-size:cover;
}
</style>